@import './src/components/common/variables';
@import './src/components/common/mixins';

.counterContainer {

  padding: 50px;
  min-height: 100%;
  background-color: $backColor;
}

.wrapper {
  width: 40vw;
  margin: auto;
  border: solid 5px $borderColor;
  border-radius: 20px;
}

.buttonsContainer {
  font-size: 0.1vw;
  color: $borderColor;
  border: 5px solid $borderColor;
  border-radius: 20px;
  padding: 2vw 0;
  margin: 3vw 3vw;
  display: flex;
  justify-content: space-around;

}


.settings {
  display: flex;
  justify-content: center;
  min-height: 100%;
  align-items: center;
  background-color: $backColor;
}

.inputs {
  border: 5px solid $borderColor;
  border-radius: 20px;
  padding: 2vw 0;
  margin: 3vw 3vw;
}

.h3AndInput {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1vw 1vw;
}

.h3AndInput h3 {
  white-space: nowrap;
  font-size: 2vw;
  padding-right: 3vw;
  color: $borderColor;
}

.inputs input {
  font-size: 1.5vw;
  border-radius: 10px;
  padding: 1vw 0;
  text-align: center;
}

.errorFontStyle {
  @include error-text(28px);
}

@media screen and (max-width: 900px) {
  .counterContainer {
    padding: 40px 10px;
  }

  .wrapper {
    width: 80vw;
    height: 80vw;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
  }
  .inputs input {
    font-size: 2.5vw;
    border-radius: 10px;
    padding: 2vw 0;
    text-align: center;
  }
  .h3AndInput h3 {
    font-size: 3.5vw;
    padding-right: 4vw;
    color: $borderColor;
  }
}